/**
 * Ð°Ð´Ð°Ð¿ÑÐ¸ÑÑÐµÑ Ð·Ð°Ð¿ÑÐ¾Ñ Ðº ÑÐµÑÐ²Ð¸ÑÑ Ð¿Ð¾Ð´ Ð½ÑÐ¶Ð½ÑÐµ Ð¿Ð°ÑÐ°Ð¼ÐµÑÑÑ (page, perPage).
 * ÐÑÐ¸Ð½Ð¸Ð¼Ð°ÐµÑ Ð¿Ð°ÑÐ°Ð¼ÐµÑÑÐ¾Ð¼ Ð¾Ð±ÑÐµÐºÑ Ñ ÐºÐ»ÑÑÐ°Ð¼Ð¸:
 *
 * doFetch - Ð°ÑÐ¸Ð½ÑÑÐ¾Ð½Ð½Ð°Ñ ÑÑÐ½ÐºÑÐ¸Ñ Ð¾Ð±ÑÐ°ÑÐµÐ½Ð¸Ñ Ðº ÑÐµÑÐ²Ð¸ÑÑ,
 * Ð²Ð¾Ð·ÑÐ°ÑÐ°ÐµÑ ÑÐ¿Ð¸ÑÐ¾Ðº Ð¾Ð±ÑÐµÐºÑÐ¾Ð² Ð´Ð»Ñ Ð´Ð°Ð»ÑÐ½ÐµÐ¹ÑÐµÐ¹ Ð¾Ð±ÑÐ°Ð±Ð¾ÑÐºÐ¸;
 *
 * sPerPage - ÐºÐ¾Ð»-Ð²Ð¾ Ð¾Ð±ÑÐµÐºÑÐ¾Ð², ÐºÐ¾ÑÐ¾ÑÑÐµ Ð²Ð¾Ð·Ð²ÑÐ°ÑÐ°ÐµÑ ÑÑÐ½ÐºÑÐ¸Ñ doFetch;
 *
 * perPage - ÐºÐ¾Ð»-Ð²Ð¾ Ð¾Ð±ÑÐµÐºÑÐ¾Ð², ÐºÐ¾ÑÐ¾ÑÑÐµ Ð¼Ñ ÑÐ¾ÑÐ¸Ð¼ Ð²Ð¸Ð´ÐµÑÑ Ð½Ð° ÑÑÑÐ°Ð½Ð¸ÑÐµ;
 *
 * page - ÑÐµÐºÑÑÐ°Ñ ÑÑÑÐ°Ð½Ð¸ÑÐ°.
 *
 *
 */
export async function adoptPPFetch({ page, perPage, sPerPage = 20, doFetch }) {
  const from = (page - 1) * perPage;
  const to = page * perPage;
  let results = [];
  let buffer = [];

  // --------------------------------------------------------
  // Ð¿ÐµÑÐ²ÑÐ¹ Ð·Ð°Ð¿ÑÐ¾Ñ,
  // Ð½Ð°Ð¼ Ð½ÑÐ¶Ð½Ð¾ 0..8(< 9),
  // ÑÐµÑÐ²Ð¸Ñ Ð²ÐµÑÐ½ÐµÑ 0..19(< 20)
  //
  // [0,1..7,8,9,10..16,17,18,19]
  // [0,1..7,8]
  //
  // from = 0
  // to   = 9
  //
  // --------------------------------------------------------
  // Ð²ÑÐ¾ÑÐ¾Ð¹ Ð·Ð°Ð¿ÑÐ¾Ñ,
  // Ð½Ð°Ð¼ Ð½ÑÐ¶Ð½Ð¾ 9..17 (<18),
  // ÑÐµÑÐ²Ð¸Ñ Ð²ÐµÑÐ½ÐµÑ 0..19(<20)
  //
  // [0,1..7,8,9,10..16,17,18,19]
  //          [9,10..16,17]
  //
  // from = 9
  // to   = 18
  //
  // --------------------------------------------------------
  // Ð¿ÐµÑÐ²ÑÐ¹ Ð·Ð°Ð¿ÑÐ¾Ñ,
  // Ð½Ð°Ð¼ Ð½ÑÐ¶Ð½Ð¾ 18..26(<27),
  // Ð´Ð²Ð° Ð·Ð°Ð¿ÑÐ¾ÑÐ° Ðº ÑÐµÑÐ²Ð¸ÑÑ,
  // ÑÐµÑÐ²Ð¸Ñ Ð²ÐµÑÐ½ÐµÑ  0..19(< 20)
  // ÑÐµÑÐ²Ð¸Ñ Ð²ÐµÑÐ½ÐµÑ 20..39(< 40)
  //
  // [0,1..7,8,9,10..16,17,18,19][20,21..25,26,27..38,39]
  //                      [18,19][20,21..25,26]
  //
  // from = 18
  // to   = 27
  //
  // --------------------------------------------------------

  //ÐÑÐ¸ Ð¿ÐµÑÐ²Ð¾Ð¼ Ð·Ð°Ð¿ÑÐ¾ÑÐµ Ð±ÑÑÐµÑ Ð·Ð°Ð¿Ð¾Ð»Ð½ÑÐµÑÑÑ Ð½Ð° 20 Ð¾Ð±ÑÐµÐºÑÐ¾Ð²
  for (let i = from; i < to; ) {
    if (buffer[i]) {
      // Ð·Ð½Ð°ÑÐµÐ½Ð¸Ðµ Ð¿Ð¾ Ð¸Ð½Ð´ÐµÐºÑÑ ÑÑÑÐµÑÑÐ²ÑÐµÑ
      results.push(buffer[i]);
      i++;
    } else {
      // Ð·Ð°Ð³ÑÑÐ¶Ð°ÐµÐ¼ ÑÐ»ÐµÐ´ÑÑÑÑÑ Ð¿Ð¾ÑÑÐ¸Ñ Ð´Ð°Ð½Ð½ÑÑ

      // Math.floor ÑÐ°Ðº ÐºÐ°Ðº Ð½ÐµÐ¾Ð±ÑÐ¾Ð´Ð¸Ð¼Ð° ÑÐµÐ»Ð°Ñ ÑÐ°ÑÑÑ
      // Ð¾Ñ Ð´ÐµÐ»ÐµÐ½Ð¸Ñ Ð¾ÐºÑÑÐ³Ð»ÐµÐ½Ð½Ð°Ñ Ð² Ð½Ð¸Ð¶Ð½ÑÑ ÑÑÐ¾ÑÐ¾Ð½Ñ.
      const sp = Math.floor(i / sPerPage) + 1;
      const sResults = await doFetch(sp);
      const sFrom = (sp - 1) * sPerPage;
      const sTo = sp * sPerPage;
      let x = 0;
      for (let j = sFrom; j < sTo; j++) {
        const sResult = sResults[x++];
        buffer[j] = { sPage: sp, ...sResult };
      }
    }
  }
  return results;
}
